import React from "react";

// css
import { Accordion, Card, Form } from "react-bootstrap";

// icon
import { FcFolder, FcFile } from "react-icons/fc";


const Folder = ({ idx, explorer, handleSelectFolder, selectedFolder }) => {


  if (explorer.items) {
      return (
      <Accordion className="CustomAccordion" defaultActiveKey="0">
        <Accordion.Item className={`shadow ${idx===0 ? "" : "Item"}`}>
          <Accordion.Header>
            <Form.Check
              type="radio"
              name="select-image-folder"
              onClick={(e=>e.stopPropagation())}
              onChange={()=>handleSelectFolder(explorer)}
              checked={Object.keys(selectedFolder).length && selectedFolder.path===explorer.path}
            />
            <FcFolder />
            {explorer.name}
          </Accordion.Header>
          <Accordion.Body>
            {explorer.items.map((exp) => {
                  return (
                    <Folder
                      key={exp.path}
                      idx={idx+1}
                      explorer={exp}
                      handleSelectFolder={handleSelectFolder}
                      selectedFolder={selectedFolder} />
                  );
              })}
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>)
  }else {
    return (
      <div className="File">
          <FcFile />{explorer.name}
      </div>
    );
  }
    
}

function FileExplorer({userFiles, handleSelectFolder, selectedFolder}) {

  return (
    <div>
          {
              Object.keys(userFiles).length === 0 ?
                (
                  <Card className="shadow CustomCard">
                    <Card.Body className="AnalysisInfo">
                      폴더를 선택하지 않았습니다.
                    </Card.Body>
                  </Card>
                )
              : (
              <Folder
                idx={0}
                explorer={userFiles}
                handleSelectFolder={handleSelectFolder}
                selectedFolder={selectedFolder}
              />
              )
          }
      </div>
  );
}

export default FileExplorer;